@charset "UTF-8";
/* Base */
/* Ховер на десктопах */
*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  font-family: system-ui;
  background: #fff;
  color: #000; }

input,
button {
  -webkit-appearance: none; }

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0; }

.container {
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px; }

label {
  display: inline-flex;
  align-items: center;
  font-size: 14px;
  cursor: pointer; }
  label input[type='text'] {
    border: 1px solid #5e5e5e;
    padding: 7px 10px;
    border-radius: 3px;
    transition: border-color 0.3s ease; }
    label input[type='text']:focus::placeholder {
      opacity: 0; }
    @media (min-width: 1080px) {
      label input[type='text']:hover, label input[type='text']:focus {
        border-color: #000; } }
  label input[type='checkbox'] {
    display: none; }
    label input[type='checkbox']:checked + b::before {
      display: block; }
  label b {
    width: 20px;
    height: 20px;
    padding: 3px;
    border: 1px solid #5e5e5e;
    border-radius: 3px;
    margin-right: 8px;
    position: relative; }
    label b::before {
      content: '';
      display: none;
      width: 100%;
      height: 100%;
      border-radius: 3px;
      background-color: #5e5e5e; }

.color-current {
  background: #eaed13 !important; }

.color-ready {
  background: #1bde4f !important; }

.hide-down {
  transform: translateY(-20px);
  opacity: 0;
  position: absolute;
  pointer-events: none; }

/* Libs */
@font-face {
  font-family: 'swiper-icons';
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal; }

:root {
  --swiper-theme-color: #007aff; }

.swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1; }

.swiper-container-vertical > .swiper-wrapper {
  flex-direction: column; }

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box; }

.swiper-container-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0); }

.swiper-container-multirow > .swiper-wrapper {
  flex-wrap: wrap; }

.swiper-container-multirow-column > .swiper-wrapper {
  flex-wrap: wrap;
  flex-direction: column; }

.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: ease-out;
  margin: 0 auto; }

.swiper-container-pointer-events {
  touch-action: pan-y; }
  .swiper-container-pointer-events.swiper-container-vertical {
    touch-action: pan-x; }

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform; }

.swiper-slide-invisible-blank {
  visibility: hidden; }

/* Auto Height */
.swiper-container-autoheight,
.swiper-container-autoheight .swiper-slide {
  height: auto; }

.swiper-container-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height; }

/* 3D Effects */
.swiper-container-3d {
  perspective: 1200px; }
  .swiper-container-3d .swiper-wrapper,
  .swiper-container-3d .swiper-slide,
  .swiper-container-3d .swiper-slide-shadow-left,
  .swiper-container-3d .swiper-slide-shadow-right,
  .swiper-container-3d .swiper-slide-shadow-top,
  .swiper-container-3d .swiper-slide-shadow-bottom,
  .swiper-container-3d .swiper-cube-shadow {
    transform-style: preserve-3d; }
  .swiper-container-3d .swiper-slide-shadow-left,
  .swiper-container-3d .swiper-slide-shadow-right,
  .swiper-container-3d .swiper-slide-shadow-top,
  .swiper-container-3d .swiper-slide-shadow-bottom {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 10; }
  .swiper-container-3d .swiper-slide-shadow-left {
    background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0)); }
  .swiper-container-3d .swiper-slide-shadow-right {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0)); }
  .swiper-container-3d .swiper-slide-shadow-top {
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0)); }
  .swiper-container-3d .swiper-slide-shadow-bottom {
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0)); }

/* CSS Mode */
.swiper-container-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */ }
  .swiper-container-css-mode > .swiper-wrapper::-webkit-scrollbar {
    display: none; }

.swiper-container-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start; }

.swiper-container-horizontal.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory; }

.swiper-container-vertical.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory; }

/* Partials */
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 150px;
  width: 100%;
  padding: 13px;
  border-radius: 22px;
  background-color: #349cd9;
  font-size: 18px;
  line-height: 18px;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s ease; }
  @media (min-width: 1080px) {
    .button:hover, .button:focus {
      background-color: rgba(52, 156, 217, 0.85); } }

/* Blocks */
.sort-page {
  padding: 40px 0; }
  .sort-page__title {
    font-size: 32px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 15px; }
  .sort-page__subtitle {
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    margin-bottom: 60px; }
  .sort-page__slider {
    position: relative;
    overflow: hidden; }

.sort-block {
  width: 100%;
  height: 50px;
  position: relative; }
  .sort-block__item {
    border-radius: 50%;
    height: 50px;
    width: 50px;
    margin: 0 0 10px;
    border: 1px solid #717d99;
    background-image: radial-gradient(circle at 50% 50%, #caf9ff 0, #9db9f2 50%, #717d99 100%);
    position: absolute;
    top: 0;
    transition: transform 0.5s ease; }
    .sort-block__item span {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 60%;
      height: 60%;
      background-color: #fff;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 18px;
      font-weight: 700; }

.step__text {
  font-size: 16px;
  line-height: 18px;
  text-align: center; }
  .step__text span {
    font-size: 14px;
    font-style: italic;
    color: #999; }

.step__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px; }

.step__field {
  margin-bottom: 10px; }

.step__create-btn, .step__sort-btn, .step__start {
  margin-top: 20px; }

.result {
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  background-color: #e6e6e6;
  margin: 40px 0;
  padding: 20px; }
  .result__title {
    font-size: 24px;
    font-weight: 500;
    font-style: italic;
    margin-bottom: 25px; }
